header {
    background-color: #333;
    color: white;
    padding: 10px;
  }
  
  header nav ul {
    list-style: none;
    padding: 0;
    display: flex;
    justify-content: space-around;
  }
  
  header nav li {
    margin: 0 10px;
  }
  
  header nav a {
    color: white;
    text-decoration: none;
  }